import * as React from "react"

// styles
const pageStyles = {
  color: "#232129",
  padding: 40,
  fontFamily: "-apple-system, Roboto, sans-serif, serif",
}

const warningStyle = {
  border: '5px solid #ff4747',
  background: '#ff7a7a',
  padding: '.8em',
  borderRadius: '30px',
}
const codeBlock = {
  fontFamily: 'monospace',
  fontSize: 'inherit',
  background: '#eee',
  color: '#000',
  padding: '1em',
  borderRadius: '20px'
}

function SingleRecording({ pageContext }) {
    const { recording } = pageContext
    return (
      <main style={pageStyles}>
        {recording.warnings.length > 0 &&
          <div style={warningStyle}>
            <h2>Warnings</h2>
            <ul>
              {recording.warnings.map((note) => {
                return <li>{note}</li>
              })}
            </ul>
          </div>
        }
        <title>{recording.date}</title>
        <h1>
          {recording.artist}<br />
          {recording.date}<br />
          {recording.venue}<br />
          {recording.city}<br />
        </h1>
        <h2>Source</h2>
        <h3>Name</h3>
        {recording.source.name}
        <h3>Recording equipment</h3>
        {recording.source.recordingEquipment}
        <h3>Generation</h3>
        {recording.generation}
        <h2>Tracks</h2>
        <h3>Running time</h3>
        {recording.times.join(', ')}
        <ol>
          {recording.tracks.map((track) => {
            return <li>{track}</li>
          })}
        </ol>
        <h2>Notes</h2>
        <ul>
          {recording.notes.map((note) => {
            return <li>{note}</li>
          })}
        </ul>

        <h2>MD5 checksums</h2>
        <ul>
          {recording.checksums.map((track) => {
            return <li>{track}</li>
          })}
        </ul>
        <h2>Surfaced on</h2>
        {recording.surfaced}
        <h2>Extracted by</h2>
        {recording.extractedBy}
        <h2>Extraction date</h2>
        {recording.extraction}
        <h2>Transferred by</h2>
        {recording.transferredBy}
        <h2>Transfer details</h2>
        {recording.transferDetails}
        <h2>Authored by</h2>
        {recording.authored}

        <hr />

        <h2>Plain text from data</h2>

        <div style={codeBlock}>
          {recording.warnings.length > 0 &&
            <div>
              Warnings
              <ul>
                {recording.warnings.map((note) => {
                  return <li>{note}</li>
                })}
              </ul>
            </div>
          }
          {recording.artist}<br />
          {recording.date}<br />
          {recording.venue}<br />
          {recording.city}<br /><br />
          Source<br /><br />
          Name<br />
          {recording.source.name}<br /><br />

          Recording equipment<br />
          {recording.source.recordingEquipment}<br /><br />

          Generation<br /><br />
          {recording.generation}<br /><br />
          Tracks<br /><br />
          Running time<br /><br />
          {recording.times.join(', ')}
          <ol>
            {recording.tracks.map((track) => {
              return <li>{track}</li>
            })}
          </ol>
          Notes
          <ul>
            {recording.notes.map((note) => {
              return <li>{note}</li>
            })}
          </ul>

          MD5 checksums
          <ul>
            {recording.checksums.map((track) => {
              return <li>{track}</li>
            })}
          </ul>
          Surfaced on<br />
          {recording.surfaced}<br /><br />
          Extracted by<br />
          {recording.extractedBy}<br /><br />
          Extraction date<br />
          {recording.extraction}<br /><br />
          Transferred by<br />
          {recording.transferredBy}<br /><br />
          Transfer details<br />
          {recording.transferDetails}<br /><br />
          Authored by<br />
          {recording.authored}<br /><br />
        </div>

        <hr />

        <h2>Original text file</h2>

        <div style={codeBlock}>
          {recording.raw.map((line, index) => {
            if (line === '') {
              return <br />
            } else {
              return <div key={`line-${index}`}>{line}</div>;
            }
          })}
        </div>
      </main>
    )
}

export default SingleRecording